.home-container {
    text-align: center;
    padding: 20px;
  }
  
  .hero {
    
    color: white;
    padding: 50px 20px;
    border-radius: 10px;
  }
  
  .nav-links {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
  }
  
  .nav-button {
    text-decoration: none;
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border-radius: 5px;
    transition: background-color 0.3s;
  }
  
  .nav-button:hover {
    background-color: #0056b3;
  }
  